.navPath {
    background-color: transparent;
    margin-bottom: 0px;
}
.Product {padding-top: 15px;}
.appBasic {padding: 0px 0px;}
/* .appBasic .icon {
    width: 12rem;
} */
.appBasic .icon img { 
    border-width: 1px; border-style: solid; 
    border-color: rgba(200, 200, 200, 0.5); 
    border-radius: 1.6rem; 
    width:12rem; height: 12rem;
}
.appBasic h3{padding-top: 0.5rem;}
.appBasic h3 code{margin: 1px 3px;padding: 0px 5px;}
.appBasic .install {float: right;}
.screenshots {
    width: 100%;
	height: auto;
	/* background-color: rgba(0,0,0,0.8); */
	white-space: nowrap;
	overflow: hidden;
	overflow-x: scroll; /* 1 */
	-webkit-backface-visibility: hidden;
    -webkit-perspective: 1000;	
    backface-visibility: hidden;
	perspective: 1000;
	-webkit-overflow-scrolling: touch; /* 2 */
	text-align: justify; /* 3 */
}
.screenshots img {
    padding: 0px;
    margin: 0px 8px;
    max-width: 54%;
}
.screenshots img#i1 {
    margin-left: 0px;
}
.screenshots img#i6 {
    margin-right: 0px;
}



