.Products {width: 100%!important;}
.Products ul {-ms-flex-wrap: wrap;flex-wrap: wrap;list-style-type: none; display: -ms-inline-flexbox; display: inline-flex; -webkit-margin-before: 0px; -webkit-margin-after: 0px; -webkit-padding-start: 0px; -webkit-padding-end:0px;}
.Products .container {margin: 1.8rem 1rem;}
.Products h4 {font-weight: 400;}
.Products .noProducts {margin-bottom: 40px;}
/* .Products .content div{max-height: 100px;} */
.Products .content hr{margin: 4px 0px;}
.ProductItem {
    background: rgba(100%, 100%, 100%, 0.5); 
    padding: 14px 12px; 
    margin: 7px 4px; 
    cursor: pointer;
    border-width: 0px; 
    border-style: solid; 
    /* border-color: rgba(180, 180, 180, 0.1); 
    box-sizing: border-box; */
}
.ProductItem .icon img{ border-width: 0.5px; 
    border-style: solid; border-color: rgba(200, 200, 200, 0.5); 
    border-radius: 1.2rem; width:96px; height: auto;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.ProductItem .title {font-size: 14px; 
    margin-top: 0.6rem;margin-bottom: 0.1rem;
    color:#667;white-space: nowrap;
    overflow: hidden;-o-text-overflow: ellipsis;text-overflow: ellipsis; width:130px;}
.ProductItem .subtitle {font-size: 11px; color: #aaa;}

.Products #platforms h3 {margin-bottom: 0px; padding-bottom: 0px;}



