
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.navbar {margin-bottom: 0px; 
  background-color: rgba(0,0,0,0.8); 
  border-width: 0px;
  border-bottom: 1px solid #000;
}
.navbar-brand {text-decoration:none;font-size:1.3em;padding: 5px 0.2rem;}
.navbar-brand td{color:#92e5fc; font-weight: 800;}
.navbar-brand #logo{-webkit-animation: App-logo-spin infinite 20s linear;animation: App-logo-spin infinite 20s linear;height: 38px;margin-left:-5px;margin-right:-5px;}

.nav .current {opacity: 0.5;cursor: not-allowed}

.sticky {
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -ms-sticky;
  position: -o-sticky;
  position: sticky;
  top: 0px;
  z-index: 100;
}
