/*
 * Footer
 *
 * Separated section of content at the bottom of all pages, save the homepage.
 */

.Footer {
  margin-top: 0px;
  color: #616465;
  text-align: center;
  /* background-color: #2a2730; */
}
.Footer #band1 {
    height: 3px;
      background-color: #2a2730;  
     /* background-color: #616465;  */
}
.Footer #band2 {
    height: 1px;
    margin-top: 2px;
     background-color: #2a2730; 
    /* background-color: #616465; */
}

.Footer .container{
  padding-top: 20px;
  padding-bottom: 50px;
}
.Footer a {
  color: #2a2730;
}
.Footer-links {
  padding-left: 0;
  margin-bottom: 20px;
}
.Footer-links li {
  display: inline-block;
}
.Footer-links li + li {
  margin-left: 15px;
}

@media (min-width: 768px) {
  .Footer {
    text-align: left;
  }
  .Footer p {
    margin-bottom: 0;
  }
}




